import Vector from './utils/vector';

export default class LinePoint {
  constructor({ x, y, angle }) {
    this.position = new Vector({ x, y });
    this.angle = new Vector().setAngle(angle);
  }

  update({ x, y }, velocity) {
    this.position.set(x, y);
    this.angle.setAngle(velocity.getAngle());
  }

  render(char, { fontSize, fontWeight }) {
    // ctx.save();
    // ctx.beginPath();
    // ctx.fillStyle = 'black';
    // ctx.arc(this.position.x, this.position.y, 3, 0, Math.PI * 2);
    // ctx.fill();
    // ctx.restore();

    // let x = radius => (Math.cos(this.angle.getAngle()) * radius) + this.position.x;
    // let y = radius => (Math.sin(this.angle.getAngle()) * radius) + this.position.y;

    // ctx.beginPath();
    // ctx.strokeStyle = 'red';
    // ctx.moveTo(x(1), y(1));
    // ctx.lineTo(x(10), y(10));
    // ctx.stroke();
    // ctx.restore();

    ctx.save();
    ctx.font = `${ fontWeight } ${ fontSize }px Helvetica`;
    // ctx.strokeStyle = 'red';
    let size = ctx.measureText(char);
    ctx.translate(this.position.x, this.position.y);
    ctx.rotate(this.angle.getAngle() + Math.PI);
    ctx.fillText(char, -(fontSize / 2), (fontSize * 0.3));


    ctx.restore();

    return size;
  }
}
