import Pointer from './Pointer';
import LinePoint from './LinePoint';

export default class SnakeText {
  constructor(x, y, word, { letterSpacing = 25, fontSize = 48, fontWeight = 'bold' } = {}, offset) {
    this.letters = word.split('');

    this.text = {
      letterSpacing,
      fontSize,
      fontWeight
    };

    this.offset = offset;

    this.header = new Pointer({ x, y });
    this.line = new Array(1000).fill(0).map((v, idx) => {
      return new LinePoint({
        x: this.header.position.x + (idx * 1),
        y: this.header.position.y,
        angle: this.header.velocity.getAngle()
      })
    });
  }

  _cascadeLine() {
    for (let i = this.line.length - 1; i >= 0; i--) {
      if (i == 0) {
        this.line[i].update(this.header.position, this.header.velocity);
      } else {
        this.line[i].update(this.line[i - 1].position, this.line[i - 1].angle)
      }
    }
  }

  update(frame, width, height) {
    this.header.update(frame, width, height);
    this._cascadeLine();
  }

  render() {
    // this.header.render();
    let letterCount = 0;

    let nextLetter = 0;

    for (let i = 0; i < this.line.length; i++) {
      if (i === nextLetter) {
        if (letterCount < this.letters.length) {
          let size = this.line[i].render(this.letters[letterCount], this.text);

          nextLetter += Math.round(size.width);

          letterCount++;
        }
      }
    }
  }
};
