import Engine from './utils/engine';
// import Engine from './utils/engine3d';

import SnakeText from './SnakeText';

const WIDTH = 480;
const HEIGHT = 480;

const el = document.getElementById('canvas');
// const el = document.getElementById('container');

const app = new Engine(el, {
  debug: false,
  width: WIDTH,
  height: HEIGHT
});

const text = [
  new SnakeText(50, 130, 'Modern Baseball', {
    fontSize: 48,
    fontWeight: 'bold'
  }, 80),
  new SnakeText(42, 180, 'Beach Slang', {
    fontSize: 32,
    fontWeight: 'bold'
  }, 60),
  new SnakeText(37, 216, 'Chumped', {
    fontSize: 21,
    fontWeight: 'bold'
  }, 40),
  new SnakeText(37, 250, 'Crow Bait', {
    fontSize: 21,
    fontWeight: 'bold'
  }, 20)
];

app.onRender(({ frame, width, height, debug }) => {
  ctx.clearRect(0, 0, width, height);

  ctx.font = `bold 12px Helvetica`;
  ctx.fillText('Noisey presents', 30, 50);

  ctx.save();
  ctx.beginPath();
  ctx.fillStyle = 'orange';
  ctx.moveTo(width, 0);
  ctx.lineTo(0, height);
  ctx.lineTo(width, height);
  ctx.fill();
  ctx.restore();

  for (let i = 0; i < text.length; i++) {
    if (text[i].offset < frame) {
      text[i].update(frame, width, height);
    }
    text[i].render();
  }

  ctx.save();
  ctx.translate(30, 380);
  ctx.font = `bold 12px Helvetica`;
  ctx.fillText('Friday 5th September', 0, 0);
  ctx.fillText('Brooklyn Bazaar', 0, 20);
  ctx.fillText('Free Entry', 0, 40);
  ctx.restore();
});

app.start();
